import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Modal, Button, Form } from "react-bootstrap";
import api from "./api";
import "../css/surveyrespose.css";
const ManageTags = () => {
  const [selectedTags, setSelectedTags] = useState([]);
  const [data, setData] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [currentTag, setCurrentTag] = useState({ id: null, tag_name: "" });
  const [showAddModal, setShowAddModal] = useState(false);
  const [currentAddTag, setCurrentAddTag] = useState({
    id: null,
    tag_name: "",
  });

  const [segment, setSegment] = useState([]);
  const [showEditSegment, setShowEditSegment] = useState(false);
  const [showAddSegment, setShowAddSegment] = useState(false);
  const [currentSegment, setCurrentSegment] = useState({
    id: null,
    segment_name: "",
  });
  const [currentAddSegment, setCurrentAddSegment] = useState({
    id: null,
    tag_name: "",
  });
  const [showTagSelectionList, setShowTagSelectionList] = useState(false);
  const [currentTagSelectionList, setCurrentTagSelectionList] = useState({
    id: null,
  });
  const [updatedSegments, setUpdatedSegments] = useState([]);
  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);
  const [segmentToDelete, setSegmentToDelete] = useState(null);
  const [tagToDelete, setTagToDelete] = useState(null);
  const [showDeleteTagConfirmation, setShowDeleteTagConfirmation] =
    useState(false);
  const [showTagSuccessModal, setTagShowSuccessModal] = useState(false);
  const [tagactionType, setTagActionType] = useState(null);
  const [showSegmentSuccessModal, setSegmentShowSuccessModal] = useState(false);
  const [segmentactionType, setSegmentActionType] = useState(null);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const response = await api.get("get_tags");

        console.log("Full Response:", response);

        if (response.status === 200 && response.data.tags) {
          const tagsData = response.data.tags || [];
          console.log("Tags Data:", tagsData);
          setData(tagsData);
        } else {
          console.log("No tags available");
          setData([]);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
        if (error.response) {
          console.error("Error Response:", error.response.data);
        } else if (error.request) {
          console.error("Error Request:", error.request);
        } else {
          console.error("Error Message:", error.message);
        }
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const response = await api.get("get_segments");

        const segmentData = response.data.data || [];
        console.log("Segment Data:", segmentData);
        setSegment(segmentData); // Set segment data
      } catch (error) {
        console.error("Error fetching data:", error);
        if (error.response) {
          console.error("Error Response:", error.response.data);
        } else if (error.request) {
          console.error("Error Request:", error.request);
        } else {
          console.error("Error Message:", error.message);
        }
      } finally {
        setLoading(false); // End loading
      }
    };

    fetchData(); // Call the fetchData function when the component mounts
  }, []);

  useEffect(() => {
    console.log("Current selectedTags:", selectedTags);
  }, [selectedTags]);
  useEffect(() => {
    setUpdatedSegments(segment);
  }, [segment]);

  useEffect(() => {
    if (segment.length > 0 && data.length > 0) {
      const updatedSegments = segment.map((seg) => {
        const tagId = seg.tag_id || ""; // Default to empty string
        const tagIds = Array.isArray(tagId)
          ? tagId
          : tagId
          ? tagId.split(",").map((id) => id.trim())
          : [];

        const tagNames = tagIds
          .map((tagId) => {
            const foundTag = data.find((t) => t.tag_id === String(tagId));
            return foundTag ? foundTag.tag_name : null;
          })
          .filter(Boolean);

        return {
          ...seg,
          tag_names: tagNames.join(", "), // No default string for no tags
        };
      });

      console.log("Updated segments:", updatedSegments);
      setUpdatedSegments(updatedSegments);
    }
  }, [segment, data]);

  if (!data) {
    return <p>Loading...</p>;
  }
  const openEditModal = (tag) => {
    setCurrentTag(tag);
    console.log("tag id value", currentTag);
    setShowModal(true);
  };
  const openAddModel = () => {
    setCurrentAddTag({ tag_name: "" });
    setShowAddModal(true);
  };
  const openEditSegment = (segment) => {
    console.log("Selected segment for editing:", segment);
    setCurrentSegment(segment);
    setShowEditSegment(true);
  };
  const openAddSegment = () => {
    setCurrentAddSegment({ tag_name: "" });
    setShowAddSegment(true);
  };

  const handleEdit = async (event) => {
    setError("");
    event.preventDefault();
    console.log("Updating tag:", currentTag);

    const { tag_id, tag_name } = currentTag; // Extract `tag_id` and `tag_name` from currentTag
    if (!tag_id || !tag_name) {
      alert("Invalid tag data.");
      return;
    }
    try {
      // Send the `tag_id` and `tag_name` together in the request body
      const response = await api.put(`update_tag`, {
        tag_id,
        tag_name,
      });
      console.log("Server response:", response.data);

      setTagShowSuccessModal(true);
      setTagActionType("edit");
      setShowModal(false);

      // Update the local state to reflect the changes
      setData((prevData) =>
        prevData.map((tag) =>
          tag.tag_id === tag_id ? { ...tag, tag_name } : tag
        )
      );
    } catch (err) {
      if (err.response) {
        setError(err.response.data.message);
      } else if (err.request) {
        setError("No response received from server");
      } else {
        setError("An error occurred while adding contact");
      }
    }
  };

  const handleAdd = async (event) => {
    setError(""); // Reset error before starting the operation
    event.preventDefault();

    const { tag_name } = currentAddTag;
    console.log("Add tag:");

    try {
      const response = await api.post("add_tag", { tag_name });
      setTagShowSuccessModal(true);
      setTagActionType("add");

      setData((prevData) => [
        ...prevData,
        { tag_id: response.data.tag_id, tag_name },
      ]);

      setShowAddModal(false);
      console.log("data", data);
    } catch (err) {
      if (err.response) {
        setError(err.response.data.message);
      } else if (err.request) {
        setError("No response received from server");
      } else {
        setError("An error occurred while adding contact");
      }
    }
  };

  const deleteTag = (id) => {
    setTagToDelete(id);
    setShowDeleteTagConfirmation(true);
  };
  const handledeleteTag = async () => {
    try {
      // Use Axios `delete` method with the configured `api` instance
      await api.delete(`delete_tag`, {
        data: { tag_id: tagToDelete }, // Provide `tag_id` in the request body
      });

      // Update local state after deletion
      setData((prevData) =>
        prevData.filter((item) => item.tag_id !== tagToDelete)
      );
      setShowDeleteTagConfirmation(false);
      setTagShowSuccessModal(true);
      setTagActionType("delete");
    } catch (error) {
      console.error("Error deleting tag:", error);
      alert("Failed to delete tag. Please try again.");
    }
  };

  const handleEditSegment = async (event) => {
    setError("");
    event.preventDefault();
    console.log("Updating segment:", currentSegment);
    const { segment_id, segment_name } = currentSegment;

    if (!segment_id) {
      console.error("No segment ID provided for update.");
      alert("Failed to update segment. No segment ID found.");
      return;
    }

    const newSegmentName = { segment_name };
    console.log("segment name:", newSegmentName);
    try {
      const response = await api.put(`update_segment`, {
        segment_id,
        segment_name,
      });
      console.log("Server response:", response.data);
      setSegmentShowSuccessModal(true);
      setSegmentActionType("edit");
      setShowEditSegment(false);

      setSegment((prevSegment) =>
        prevSegment.map((segment) =>
          segment.segment_id === segment_id
            ? { ...segment, segment_name }
            : segment
        )
      );
    } catch (err) {
      if (err.response) {
        setError(err.response.data.message);
      } else if (err.request) {
        setError("No response received from server");
      } else {
        setError("An error occurred while adding contact");
      }
    }
  };

  const handleAddSegment = async (event) => {
    setError("");
    event.preventDefault();
    const { segment_name } = currentAddSegment;
    console.log("Add Segment:");

    try {
      const response = await api.post(`add_segment`, { segment_name });

      setSegmentShowSuccessModal(true);
      setSegmentActionType("add");
      setShowAddSegment(false);
      setSegment((prevSegment) => [
        ...prevSegment,
        { segment_id: response.data.segment_id, segment_name },
      ]);
    } catch (err) {
      if (err.response) {
        setError(err.response.data.message);
      } else if (err.request) {
        setError("No response received from server");
      } else {
        setError("An error occurred while adding contact");
      }
    }
  };
  const deleteSegment = (id) => {
    setSegmentToDelete(id);
    setShowDeleteConfirmation(true);
  };
  const handleConfirmDelete = async () => {
    try {
      const response = await api.delete("delete_segment", {
        data: { segment_id: segmentToDelete },
      });

      if (response.status === 200) {
        setSegment((prevSegment) =>
          prevSegment.filter((item) => item.segment_id !== segmentToDelete)
        );
        setSegmentShowSuccessModal(true);
        setSegmentActionType("delete");
        setShowDeleteConfirmation(false);
      } else {
        alert("Failed to delete segment");
      }
    } catch (error) {
      console.error("Error deleting Segment:", error);
      alert("Error deleting the segment. Please try again.");
    }
  };

  const openTagSelectionList = (id) => {
    console.log("Opening tag selection for ID:", id);

    const segmentData = segment.find((seg) => seg.segment_id === id);
    console.log("Found Segment Data:", segmentData);

    if (segmentData) {
      setCurrentTagSelectionList(id);

      const tagIds = Array.isArray(segmentData.tag_id)
        ? segmentData.tag_id
        : segmentData.tag_id
        ? segmentData.tag_id.split(",").map((id) => id.trim())
        : [];

      console.log("Processed tag IDs:", tagIds);

      const selectedTags = tagIds
        .map((tagId) => {
          const tag = data.find((t) => String(t.tag_id) === String(tagId));
          if (tag) {
            return { id: String(tagId), name: tag.tag_name };
          }
          return null;
        })
        .filter(Boolean);

      console.log("Selected Tags after lookup:", selectedTags);

      setSelectedTags(selectedTags);
    } else {
      console.warn("No segment found for ID:", id);
    }

    setShowTagSelectionList(true);
  };

  const handleCheckboxChange = (e, tag) => {
    const isChecked = e.target.checked;

    setSelectedTags((prev) => {
      if (isChecked) {
        const existingTag = prev.find((t) => t.id === String(tag.tag_id));
        if (!existingTag) {
          return [...prev, { id: String(tag.tag_id), name: tag.tag_name }];
        }
      } else {
        return prev.filter((t) => t.id !== String(tag.tag_id));
      }

      return prev;
    });
  };

  const handleTagSelection = async (event) => {
    event.preventDefault();

    const tagIdsString = selectedTags.map((tag) => tag.id).join(",");
    const payload = {
      tag_id: tagIdsString,
      segment_id: currentTagSelectionList,
    };

    try {
      const response = await api.put(`update_segment_tag`, payload, {
        headers: { "Content-Type": "application/json" },
      });

      console.log("Server response:", response.data);

      if (!response.data || !response.data.tag_id) {
        console.warn("Invalid response: tag_id is null or missing");
        setSelectedTags([]);
        setSegment((prevSegments) =>
          prevSegments.map((segment) => {
            if (segment.segment_id === currentTagSelectionList) {
              return {
                ...segment,
                tag_id: "",
                tag_name: "",
              };
            }
            return segment;
          })
        );

        setShowTagSelectionList(false);
        return;
      }

      const updatedTagIds = response.data.tag_id
        .split(",")
        .map((id) => id.trim());

      setSegment((prevSegments) =>
        prevSegments.map((segment) => {
          if (segment.segment_id === currentTagSelectionList) {
            const updatedTagNames = updatedTagIds
              .map((id) => {
                const tag = data.find((t) => String(t.tag_id) === String(id));
                return tag ? tag.tag_name : null;
              })
              .filter(Boolean);

            return {
              ...segment,
              tag_id: updatedTagIds.join(","),
              tag_name: updatedTagNames.join(", "),
            };
          }
          return segment;
        })
      );

      const updatedTags = updatedTagIds
        .map((id) => {
          const tag = data.find((t) => String(t.tag_id) === String(id));
          return tag ? { id, name: tag.tag_name } : null;
        })
        .filter(Boolean);

      setSelectedTags(updatedTags);
      setSegmentShowSuccessModal(true);
      setSegmentActionType("update");
      setShowTagSelectionList(false);
    } catch (err) {
      console.error("Error updating tags:", err.message);
    }
  };

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center vh-100">
        <img src="images/spinner.gif" alt="Loading..." className="img-fluid" />
      </div>
    );
  }

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-12">
          <div className="card border-0">
            <div className="card-header border-0">
              <div
                className="d-flex justify-content-between align-items-center"
                style={{ marginBottom: "-1rem" }}
              >
                <h4 className="surveyHeader">Segment Management</h4>
                <Link
                  className="btn "
                  style={{ backgroundColor: "#0a2472", color: "white" }}
                  onClick={openAddSegment}
                >
                  Add Segment
                </Link>
              </div>
            </div>
            <div className="card-body table-container">
              <table className="table table-bordered custom-table">
                <thead>
                  <tr>
                    <th
                      className="custom-table-header"
                      style={{
                        backgroundColor: "#9ee2ff",
                        textAlign: "center",
                        width: "40%",
                      }}
                    >
                      Name
                    </th>
                    <th
                      className="custom-table-header"
                      style={{
                        backgroundColor: "#9ee2ff",
                        textAlign: "center",
                        width: "40%",
                      }}
                    >
                      Tag List
                    </th>
                    <th
                      colSpan={3}
                      className="custom-table-header"
                      style={{
                        backgroundColor: "#9ee2ff",
                        textAlign: "center",
                        width: "20%",
                      }}
                    >
                      Action
                    </th>
                  </tr>
                </thead>
                <tbody
                  className="custom-table-body"
                  style={{ backgroundColor: "#9ee2ff", textAlign: "center" }}
                >
                  {updatedSegments.map((item, index) => (
                    <tr
                      key={item.segment_id || index}
                      className="custom-table-row"
                    >
                      <td
                        className="custom-table-cell"
                        style={{ textAlign: "center" }}
                      >
                        {item.segment_name}
                      </td>
                      <td
                        className="custom-table-cell"
                        style={{ textAlign: "center" }}
                      >
                        {item.tag_name}
                      </td>
                      <td className="custom-table-cell">
                        <div className="action-btn-group">
                          <Link
                            onClick={() =>
                              openTagSelectionList(item.segment_id)
                            }
                            className="btn btn-default add-tag-btn"
                          >
                            <img
                              src="images/icon/plus.png"
                              alt="Add Tag"
                              className="action-icon"
                            />
                          </Link>
                        </div>
                      </td>
                      <td className="custom-table-cell">
                        <div className="action-btn-group">
                          <Link
                            onClick={() => openEditSegment(item)}
                            className="btn btn-default edit-tag-btn"
                          >
                            <img
                              src="images/icon/edit-1.png"
                              alt="Edit"
                              className="action-icon"
                            />
                          </Link>
                        </div>
                      </td>
                      <td className="custom-table-cell">
                        <div className="action-btn-group">
                          <Link
                            onClick={() => deleteSegment(item.segment_id)}
                            className="btn btn-default delete-tag-btn"
                          >
                            <img
                              src="images/icon/remove-1.png"
                              alt="Delete"
                              className="action-icon"
                            />
                          </Link>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
          <div className="card border-0">
            <div className="card-header border-0">
              <div className="d-flex justify-content-between align-items-center mb-3">
                <h4 className="surveyHeader">Tag Management</h4>
                <Link
                  className="btn "
                  style={{ backgroundColor: "#0a2472", color: "white" }}
                  onClick={openAddModel}
                >
                  Add Tags
                </Link>
              </div>
            </div>
            <div className="card-body">
              <table className="table table-bordered">
                <thead
                  style={{ backgroundColor: "#9ee2ff", textAlign: "center" }}
                >
                  <tr>
                    <th style={{ backgroundColor: "#9ee2ff" }}>Name</th>
                    <th
                      colSpan={2}
                      style={{
                        textAlign: "center",
                        backgroundColor: "#9ee2ff",
                      }}
                    >
                      Action
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {data.length === 0 ? (
                    <tr>
                      <td colSpan="3">No data available</td>
                    </tr>
                  ) : (
                    data.map((item, index) => (
                      <tr
                        key={item.tag_id || index}
                        style={{ textAlign: "left" }}
                      >
                        <td style={{ textAlign: "center" }}>{item.tag_name}</td>
                        <td style={{ textAlign: "center" }}>
                          <Link
                            onClick={() => openEditModal(item)}
                            type="button"
                            className="btn btn-default editTag"
                            data-bs-toggle="modal"
                            data-bs-target="#tagEditModal"
                          >
                            <img
                              src="images/icon/edit-1.png"
                              alt=""
                              className="img-centered img-fluid"
                              style={{ width: "20px", height: "20px" }}
                            />
                          </Link>
                        </td>
                        <td style={{ textAlign: "center" }}>
                          <Link
                            onClick={() => deleteTag(item.tag_id)}
                            className="btn btn-default deleteTag"
                            data-bs-toggle="modal"
                            data-bs-target="#tagDeleteModal"
                          >
                            <img
                              src="images/icon/remove-1.png"
                              alt=""
                              className="img-centered img-fluid"
                              style={{ width: "20px", height: "20px" }}
                            />
                          </Link>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>
          <Modal show={showModal} onHide={() => setShowModal(false)}>
            <Modal.Header
              closeButton
              style={{
                backgroundColor: "#0a2472",
                color: "white",
                justifyContent: "center",
              }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Edit Tag
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form>
                <Form.Group controlId="tagName">
                  <Form.Label>Tag Name</Form.Label>
                  <Form.Control
                    type="text"
                    value={currentTag.tag_name}
                    onChange={(e) =>
                      setCurrentTag({ ...currentTag, tag_name: e.target.value })
                    }
                    required
                  />
                </Form.Group>
              </Form>
            </Modal.Body>

            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "100%",
                  maxWidth: "300px",
                  margin: "0 auto",
                }}
              >
                <Button
                  onClick={handleEdit}
                  style={{
                    marginBottom: "10px",
                    width: "100%",
                    backgroundColor: "#0a2472",
                  }}
                >
                  Save
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => setShowModal(false)}
                  style={{ width: "100%" }}
                >
                  Cancel
                </Button>
                {error && <p className="error-message">{error}</p>}
              </div>
            </Modal.Footer>
          </Modal>
          <Modal show={showAddModal} onHide={() => setShowAddModal(false)}>
            <Modal.Header closeButton>
              <Modal.Title>Add Tag</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form>
                <Form.Group controlId="tagName">
                  <Form.Label>Please enter a new Tag name:</Form.Label>
                  <Form.Control
                    type="text"
                    value={currentAddTag.tag_name}
                    onChange={(e) =>
                      setCurrentAddTag({ tag_name: e.target.value })
                    }
                    required
                    placeholder="tag name"
                  />
                </Form.Group>
              </Form>
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <Button
                style={{
                  marginBottom: "10px",
                  width: "100%",
                  backgroundColor: "#0a2472",
                }}
                onClick={handleAdd}
              >
                Add Tag
              </Button>
              <Button
                variant="secondary"
                onClick={() => setShowAddModal(false)}
              >
                Close
              </Button>{" "}
              {error && <p className="error-message">{error}</p>}
            </Modal.Footer>
          </Modal>

          <Modal
            show={showEditSegment}
            onHide={() => setShowEditSegment(false)}
          >
            <Modal.Header
              closeButton
              style={{
                backgroundColor: "#0a2472",
                color: "white",
                justifyContent: "center",
              }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Edit Segment
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form>
                <Form.Group controlId="tagName">
                  <Form.Label>Segment Name</Form.Label>
                  <Form.Control
                    type="text"
                    value={currentSegment.segment_name}
                    onChange={(e) =>
                      setCurrentSegment({
                        ...currentSegment,
                        segment_name: e.target.value,
                      })
                    }
                    required
                  />
                </Form.Group>
              </Form>
            </Modal.Body>

            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "100%",
                  maxWidth: "300px",
                  margin: "0 auto",
                }}
              >
                <Button
                  onClick={handleEditSegment}
                  style={{
                    marginBottom: "10px",
                    width: "100%",
                    backgroundColor: "#0a2472",
                  }}
                >
                  Save
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => setShowEditSegment(false)}
                  style={{ width: "100%" }}
                >
                  Cancel
                </Button>{" "}
                {error && <p className="error-message">{error}</p>}
              </div>
            </Modal.Footer>
          </Modal>
          <Modal show={showAddSegment} onHide={() => setShowAddSegment(false)}>
            <Modal.Header
              closeButton
              style={{
                backgroundColor: "#0a2472",
                color: "white",
                justifyContent: "center",
              }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Create Segment
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form>
                <Form.Group controlId="SegmentName">
                  <Form.Label>Please enter a new segment name:</Form.Label>
                  <Form.Control
                    type="text"
                    value={currentAddSegment.segment_name}
                    placeholder="segment name"
                    onChange={(e) =>
                      setCurrentAddSegment({ segment_name: e.target.value })
                    }
                    required
                  />
                </Form.Group>
              </Form>
            </Modal.Body>

            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "100%",
                  maxWidth: "300px",
                  margin: "0 auto",
                }}
              >
                <Button
                  onClick={handleAddSegment}
                  style={{
                    marginBottom: "10px",
                    width: "100%",
                    backgroundColor: "#0a2472",
                  }}
                >
                  Add Segment
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => setShowAddSegment(false)}
                  style={{ width: "100%" }}
                >
                  Cancel
                </Button>{" "}
                {error && <p className="error-message">{error}</p>}
              </div>
            </Modal.Footer>
          </Modal>

          <Modal
            show={showTagSelectionList}
            onHide={() => setShowTagSelectionList(false)}
          >
            <Modal.Header
              closeButton
              style={{
                backgroundColor: "#0a2472",
                color: "white",
                justifyContent: "center",
              }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Select Tag
              </Modal.Title>
            </Modal.Header>
            <Modal.Body style={{ display: "flex", justifyContent: "center" }}>
              {data.length === 0 ? (
                <div style={{ textAlign: "center", width: "100%" }}>
                  <p>No tags found. Please add tags to continue.</p>
                </div>
              ) : (
                <ul
                  style={{
                    listStyleType: "none",
                    padding: 0,
                    width: "100%",
                    maxWidth: "400px",
                  }}
                >
                  {data.map((item) => (
                    <li key={item.tag_id} style={{ margin: "10px 0" }}>
                      <label style={{ display: "flex", alignItems: "center" }}>
                        <input
                          type="checkbox"
                          onChange={(e) => handleCheckboxChange(e, item)}
                          checked={selectedTags.some(
                            (tag) => tag.id === String(item.tag_id)
                          )} // Check if the tag is selected
                        />
                        <span style={{ marginLeft: "8px" }}>
                          {item.tag_name}
                        </span>
                      </label>
                    </li>
                  ))}
                </ul>
              )}
            </Modal.Body>
            <Modal.Footer style={{ display: "flex", justifyContent: "center" }}>
              {" "}
              {data.length > 0 && (
                <>
                  <Button
                    style={{
                      marginBottom: "10px",
                      width: "100%",
                      backgroundColor: "#0a2472",
                    }}
                    onClick={handleTagSelection}
                  >
                    Save Changes
                  </Button>
                  <Button
                    variant="secondary"
                    onClick={() => setShowTagSelectionList(false)}
                    style={{ marginRight: "10px" }}
                  >
                    Close
                  </Button>
                </>
              )}{" "}
            </Modal.Footer>
          </Modal>

          <Modal
            show={showDeleteConfirmation}
            onHide={() => setShowDeleteConfirmation(false)}
            style={{ marginTop: "50px" }}
          >
            <Modal.Header
              closeButton
              style={{ justifyContent: "center", width: "100%" }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Delete Customer
              </Modal.Title>
            </Modal.Header>
            <Modal.Body
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center",
              }}
            >
              Are you sure you want to delete the segment?
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "100%",
                  maxWidth: "300px",
                  margin: "0 auto",
                }}
              >
                <Button
                  variant="danger"
                  onClick={handleConfirmDelete}
                  style={{ marginBottom: "10px", width: "100%" }}
                >
                  Confirm
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => setShowDeleteConfirmation(false)}
                  style={{ width: "100%" }}
                >
                  Cancel
                </Button>
              </div>
            </Modal.Footer>
          </Modal>

          <Modal
            show={showDeleteTagConfirmation}
            onHide={() => setShowDeleteTagConfirmation(false)}
            style={{ marginTop: "50px" }}
          >
            <Modal.Header
              closeButton
              style={{ justifyContent: "center", width: "100%" }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Delete Customer
              </Modal.Title>
            </Modal.Header>
            <Modal.Body
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center",
              }}
            >
              Are you sure you want to delete the Tag?
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "100%",
                  maxWidth: "300px",
                  margin: "0 auto",
                }}
              >
                <Button
                  variant="danger"
                  onClick={handledeleteTag}
                  style={{ marginBottom: "10px", width: "100%" }}
                >
                  Confirm
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => setShowDeleteTagConfirmation(false)}
                  style={{ width: "100%" }}
                >
                  Cancel
                </Button>
              </div>
            </Modal.Footer>
          </Modal>

          <Modal
            show={showTagSuccessModal}
            onHide={() => setTagShowSuccessModal(false)}
            style={{ marginTop: "50px" }}
          >
            <Modal.Header
              closeButton
              style={{ justifyContent: "center", width: "100%" }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Success
              </Modal.Title>
            </Modal.Header>
            <Modal.Body style={{ textAlign: "center" }}>
              {tagactionType === "edit"
                ? "Tag Updated Successfully!"
                : tagactionType === "add"
                ? "Tag Added Successfully!"
                : tagactionType === "delete"
                ? "Tag Deleted Successfully!"
                : "Action Successful!"}
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <Button
                variant="success"
                onClick={() => setTagShowSuccessModal(false)}
              >
                Close
              </Button>
            </Modal.Footer>
          </Modal>
          <Modal
            show={showSegmentSuccessModal}
            onHide={() => setSegmentShowSuccessModal(false)}
            style={{ marginTop: "50px" }}
          >
            <Modal.Header
              closeButton
              style={{ justifyContent: "center", width: "100%" }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Success
              </Modal.Title>
            </Modal.Header>
            <Modal.Body style={{ textAlign: "center" }}>
              {segmentactionType === "edit"
                ? "Segment Updated Successfully!"
                : segmentactionType === "add"
                ? "Segment Added Successfully!"
                : segmentactionType === "delete"
                ? "Segment Deleted Successfully!"
                : segmentactionType === "update"
                ? "Tag Updated Successfully!"
                : "Action Successful!"}
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <Button
                variant="success"
                onClick={() => setSegmentShowSuccessModal(false)}
              >
                Close
              </Button>
            </Modal.Footer>
          </Modal>
        </div>
      </div>
    </div>
  );
};

export default ManageTags;
