import React, { useState, useEffect } from "react";
import api from "././api";
import "../css/surveyrespose.css";

const AnswerList = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await api.get("get_all_answers");
        setData(response.data.answers || []);
        setLoading(false);
        console.log("Fetched data:", response.data);
      } catch (error) {
        console.error("Error fetching data:", error);
        setError("Error fetching data");
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center vh-100">
        <img src="images/spinner.gif" alt="Loading..." className="img-fluid" />
      </div>
    );
  }

  if (error) {
    return <div>{error}</div>;
  }

  return (
    <div className="container-fluid min-vh-100 d-flex flex-column  py-4">
      <div className="row justify-content-center flex-grow-1">
        <div className="col-md-12">
          <div className="card" style={{ border: "none", boxShadow: "none" }}>
            <div
              className="card-header"
              style={{ borderBottom: "none", backgroundColor: "transparent" }}
            >
              <div
                className="d-flex justify-content-between align-items-center"
                style={{ marginTop: "48px" }}
              >
                <h2 className="surveyHeader">Answer List</h2>
              </div>
            </div>
            <div className="card-body">
              {data.length === 0 ? (
                <p>No answers available.</p>
              ) : (
                <table className="table" style={{ borderCollapse: "collapse" }}>
                  <thead>
                    <tr>
                      <th
                        style={{
                          backgroundColor: "#9ee2ff",
                          textAlign: "center",
                          width: "20%",
                          border: "none",
                        }}
                      >
                        S.no.
                      </th>
                      <th
                        style={{
                          backgroundColor: "#9ee2ff",
                          textAlign: "center",
                          width: "20%",
                          border: "none",
                        }}
                      >
                        Emoji
                      </th>
                      <th
                        style={{
                          backgroundColor: "#9ee2ff",
                          textAlign: "center",
                          width: "20%",
                          border: "none",
                        }}
                      >
                        Rating Star
                      </th>
                      <th
                        style={{
                          backgroundColor: "#9ee2ff",
                          textAlign: "center",
                          width: "20%",
                          border: "none",
                        }}
                      >
                        Text
                      </th>
                      <th
                        style={{
                          backgroundColor: "#9ee2ff",
                          textAlign: "center",
                          width: "20%",
                          border: "none",
                        }}
                      >
                        Numeric_value
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {data.map((item, index) => (
                      <tr key={item.answer_id || index}>
                        <td style={{ textAlign: "center", border: "none" }}>
                          {index + 1}
                        </td>
                        <td style={{ textAlign: "center", border: "none" }}>
                          <img
                            src={`http://35.91.75.215:5000/${
                              item.emoji ? item.emoji : ""
                            }`}
                            alt="Emoji"
                            style={{
                              width: "40px",
                              height: "40px",
                              objectFit: "cover",
                              borderRadius: "5px",
                            }}
                          />
                        </td>
                        <td
                          style={{
                            textAlign: "center",
                            border: "none",
                            width: "20%",
                          }}
                        >
                          <img
                            src={`http://35.91.75.215:5000/${
                              item.rating ? item.rating : ""
                            }`}
                            alt="Rating Star"
                            style={{
                              width:
                                index === 4
                                  ? "120px"
                                  : index === 3
                                  ? "100px"
                                  : index === 2
                                  ? "75px"
                                  : index === 1
                                  ? "50px"
                                  : index === 0
                                  ? "25px"
                                  : "50px",
                              height: "auto",
                              objectFit: "cover",
                              borderRadius: "5px",
                            }}
                          />
                        </td>

                        <td style={{ textAlign: "center", border: "none" }}>
                          {item.text}
                        </td>

                        <td style={{ textAlign: "center", border: "none" }}>
                          {item.numeric_value}
                        </td>

                        <td
                          style={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                            border: "none",
                          }}
                        ></td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AnswerList;
