import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import "../css/Login.css";

const ResetPassword = ({ setIsAuthenticated }) => {
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [newPasswordError, setNewPasswordError] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const [error, setError] = useState("");
  const [message, setMessage] = useState("");
  const navigate = useNavigate();
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const token = queryParams.get("token");
  const expiresAt = queryParams.get("expiresAt");

  useEffect(() => {
    if (!token || !expiresAt) {
      setError("Invalid or missing token.");
      navigate("/login");
    }

    // Check if the token has expired
    const expirationTime = parseInt(expiresAt);
    if (Date.now() > expirationTime) {
      setError("Token has expired.");
      navigate("/login");
    }
  }, [token, expiresAt, navigate]);

  const handleChangePassword = async (e) => {
    e.preventDefault();

    // Reset errors
    setNewPasswordError("");
    setConfirmPasswordError("");
    setError("");

    // Check if passwords are provided
    if (!newPassword) {
      setNewPasswordError("New password is required.");
      return;
    }

    if (!confirmPassword) {
      setConfirmPasswordError("Confirm password is required.");
      return;
    }

    // Check if passwords match
    if (newPassword !== confirmPassword) {
      setConfirmPasswordError("Passwords do not match.");
      return;
    }

    try {
      const response = await axios.post(
        "http://35.91.75.215:5000/api/auth/reset-password",
        {
          token, // Include the token from query params
          newPassword,
          confirmPassword,
        }
      );

      if (response.status === 200) {
        setMessage("Password changed successfully!");
        setTimeout(() => {
          navigate("/login"); // Redirect to login after success message
        }, 2000);
      } else {
        setError("Error changing password.");
      }
    } catch (err) {
      setError("Error changing password.");
      console.error(err);
    }
  };

  const handleBackToLogin = () => {
    navigate("/login");
  };

  return (
    <div className="login_container adaptive-container">
      <div className="row" style={{ width: "100%" }}>
        <div className="col-md-7"></div>
        <form
          className="col-md-5 sign-in adaptive-form"
          id="sign_in"
          onSubmit={handleChangePassword}
        >
          <div className="login-container adaptive-inner-container">
            <h1 className="login-title">Reset Password</h1>

            <div className="signin-field adaptive-field">
              <input
                type="password"
                className="form-control adaptive-input"
                value={newPassword}
                placeholder="New password"
                onChange={(e) => setNewPassword(e.target.value)}
              />
              {newPasswordError && (
                <p className="text-danger mt-1">{newPasswordError}</p>
              )}
            </div>

            <div className="signin-field adaptive-field">
              <input
                type="password"
                className="form-control adaptive-input"
                value={confirmPassword}
                placeholder="Confirm Password"
                onChange={(e) => setConfirmPassword(e.target.value)}
              />
              {confirmPasswordError && (
                <p className="text-danger mt-1">{confirmPasswordError}</p>
              )}
            </div>

            <button
              className="login-button btn-style Centered adaptive-button"
              type="submit"
            >
              Submit
            </button>

            <button
              className="login-button btn-style Centered adaptive-button"
              type="button"
              onClick={handleBackToLogin}
            >
              Back to Login
            </button>

            {message && (
              <p className="text-success mt-3 text-center adaptive-message">
                {message}
              </p>
            )}
            {error && (
              <p className="text-danger mt-3 text-center adaptive-error">
                {error}
              </p>
            )}
          </div>
        </form>
      </div>
    </div>
  );
};

export default ResetPassword;
