import React, { useState, useEffect } from "react";

import api from "./api";
import * as XLSX from "xlsx";
import { Table, Modal } from "react-bootstrap";
import "../css/surveyrespose.css";

const SurveyResponse = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedSurveyId, setSelectedSurveyId] = useState("");
  const [surveyDetails, setSurveyDetails] = useState(null);
  const [detailsLoading, setDetailsLoading] = useState(false);
  const [showReceipientModal, setShowReceipientModal] = useState(false);
  const [currentReceipient, setCurrentReceipient] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await api.get("sent_survey");
        const allSurveys = Array.isArray(response.data.surveys)
          ? response.data.surveys.filter(
              (item) =>
                item && item.survey_name && item.survey_name.trim() !== ""
            )
          : [];
        setData(allSurveys);

        if (allSurveys.length > 0) {
          setSelectedSurveyId(allSurveys[0].survey_id);
        }
      } catch (error) {
        setError("Error fetching surveys");
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  useEffect(() => {
    if (!selectedSurveyId) return;

    const fetchSurveyDetails = async () => {
      setDetailsLoading(true);
      try {
        const response = await api.get(
          `get_surveyById/?survey_id=${selectedSurveyId}`
        );
        setSurveyDetails(response.data);
      } catch (error) {
        setError("Error fetching survey details");
      } finally {
        setDetailsLoading(false);
      }
    };
    fetchSurveyDetails();
  }, [selectedSurveyId]);

  const formatDateAndTime = (dateString) => {
    const date = new Date(dateString);
    const time = date.toLocaleTimeString("en-US", {
      hour12: false,
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });
    const formattedDate = date.toLocaleDateString("en-US", {
      weekday: "long",
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
    });
    return { time, formattedDate };
  };

  const exportToExcel = () => {
    if (!surveyDetails || !surveyDetails.responses || !selectedSurveyId) return;

    const selectedSurvey = data.find(
      (item) => item.survey_id === selectedSurveyId
    );
    const campaignName = selectedSurvey?.survey_name || "Unknown Survey";

    const formattedData = surveyDetails.responses.map((detail) => {
      const createdAt = detail.created_at || null;
      const { time, formattedDate } = createdAt
        ? formatDateAndTime(createdAt)
        : { time: "N/A", formattedDate: "N/A" };

      return {
        "Campaign Sent time": time,
        "Campaign Sent date": formattedDate,
        Location: detail.location || "N/A",
        "Customer Name": detail.firstname || "N/A",
        "Phone Number": detail.contact_details || "N/A",
        Email: detail.email_id || "N/A",
        "Satisfaction Level": detail.satisfaction_level || "N/A",
      };
    });

    const sheetData = [
      [`Campaign Name: ${campaignName}`],
      [
        "Campaign Sent time",
        "Campaign Sent date",
        "Location",
        "Customer Name",
        "Phone Number",
        "Email",
        "Satisfaction Level",
      ],
      ...formattedData.map((detail) => Object.values(detail)),
    ];

    const ws = XLSX.utils.aoa_to_sheet(sheetData);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Survey Responses");
    XLSX.writeFile(wb, "survey_responses.xlsx");
  };
  const openRecepient = async () => {
    if (!selectedSurveyId) {
      setError("No survey selected");
      return;
    }

    try {
      const response = await api.get(
        `get_recipient?survey_id=${selectedSurveyId}`
      );
      console.log("Response Data:", response.data);

      if (response.status !== 200 || !response.data) {
        throw new Error("No recipient data found.");
      }

      const recipients = Array.isArray(response.data)
        ? response.data
        : [response.data];

      setCurrentReceipient(recipients);
      setShowReceipientModal(true);
    } catch (error) {
      console.error("Error fetching recipients:", error);
      setError(error.response?.data?.message || "Failed to fetch recipients.");
    }
  };

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center vh-100">
        <img src="images/spinner.gif" alt="Loading..." className="img-fluid" />
      </div>
    );
  }

  if (error) {
    return <div className="text-center">{error}</div>;
  }
  return (
    <div className="container-fluid min-vh-100 d-flex flex-column  py-4 mt-1">
      <div className="row justify-content-center flex-grow-1">
        <div className="col-md-12 mt-5">
          <div className="d-flex justify-content-between align-items-center mb-4">
            <h2 className="surveyHeader">Survey Responses</h2>
          </div>
          <div className="p-4">
            <div className="row">
              <div className="mb-4 row align-items-center">
                <div className="col-12 d-flex justify-content-between align-items-center flex-wrap">
                  {data.length > 0 ? (
                    <>
                      <select
                        name="survey"
                        id="survey-dropdown"
                        value={selectedSurveyId}
                        onChange={(e) => setSelectedSurveyId(e.target.value)}
                        className="form-select"
                        style={{ width: "auto", minWidth: "150px" }} // Adjust dynamically
                      >
                        {data.map((item) => (
                          <option key={item.survey_id} value={item.survey_id}>
                            {item.survey_name}
                          </option>
                        ))}
                      </select>
                      <div className="d-flex flex-column flex-md-row justify-content-end align-items-md-center mt-2 mt-md-0">
                        <button
                          onClick={openRecepient}
                          className="recepient btn  flex-shrink-0  rb"
                          style={{
                            width: "auto",
                            minWidth: "100px",
                            backgroundColor: "#0a2472",
                            color: "white",
                          }}
                        >
                          Recipient
                        </button>{" "}
                      </div>
                    </>
                  ) : (
                    <div className="w-100 d-flex flex-column align-items-center justify-content-center">
                      <p className="fs-3 noData text-center">
                        <span className="text-danger">Oops!</span> No survey
                        created yet.
                      </p>
                    </div>
                  )}
                </div>
              </div>

              <div className="col-md-12 d-flex justify-content-end mb-3">
                {surveyDetails?.responses?.length > 0 && (
                  <>
                    <p className="download-text">Download the Excel</p>
                    <button onClick={exportToExcel} className="btn-download">
                      <img
                        src="images/icon/response/download-pie.png"
                        alt="Download Icon"
                        height={"20px"}
                        width={"20px"}
                      />
                    </button>
                  </>
                )}
              </div>

              <div className="col-md-12">
                {detailsLoading ? (
                  <div className="d-flex justify-content-center align-items-center">
                    <span>Loading details...</span>
                  </div>
                ) : selectedSurveyId && surveyDetails?.responses?.length > 0 ? (
                  surveyDetails.responses.map((detail, index) => {
                    const { time, formattedDate } = formatDateAndTime(
                      detail.created_at
                    );

                    return (
                      <div key={index} className="mb-4 mt-5 py-2 py-2">
                        <div className="row mb-3">
                          <div className="col-md-3">
                            <p className="SurveyResponse">
                              <img
                                className="img-centered img-fluid"
                                src="images/icon/response/time.png"
                                alt="sent time"
                              />
                              Sent Time: {time}
                            </p>
                          </div>
                          <div className="col-md-3">
                            <p>
                              {" "}
                              <img
                                src="images/icon/response/Calendar.png"
                                alt="date"
                              />
                              {formattedDate}
                            </p>
                          </div>
                          <div className="col-md-3">
                            <p className="SurveyResponse">
                              {" "}
                              <img
                                className="img-centered img-fluid"
                                src="images/icon/response/Location.png"
                                alt="location"
                              />
                              Location: {detail.location || "N/A"}
                            </p>
                          </div>

                          <div className="col-md-3">
                            <p className="SurveyResponse">
                              <img
                                className="img-centered img-fluid"
                                src="images/icon/response/Phone.png"
                                alt="phone number"
                              />
                              Phone Number: {detail.contact_details || "N/A"}
                            </p>
                          </div>
                        </div>
                        <div className="row mb-3">
                          <p className="SurveyResponse">
                            <img
                              className="img-centered img-fluid"
                              src="images/icon/response/Email.png"
                            />
                            Email: {detail.email_id || "N/A"}
                          </p>
                        </div>
                        <div className="row mb-3">
                          <p className="SurveyResponse">
                            Satisfaction Level:{" "}
                            {detail.satisfaction_level || "N/A"}
                          </p>
                        </div>

                        {index !== surveyDetails?.responses?.length - 1 && (
                          <hr className="my-3" />
                        )}
                      </div>
                    );
                  })
                ) : selectedSurveyId ? (
                  <p className="fs-3 noData">
                    <span className="text-danger">Oops!</span>No details
                    available for this survey.
                  </p>
                ) : null}
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        className="custom-modal modal-recipients-top"
        show={showReceipientModal}
        onHide={() => setShowReceipientModal(false)}
      >
        {currentReceipient && currentReceipient.length > 0 ? (
          <Modal.Header closeButton>
            <Modal.Title>
              Recipients list - {currentReceipient[0]?.survey_name || "N/A"}
            </Modal.Title>
          </Modal.Header>
        ) : null}

        <Modal.Body>
          {currentReceipient && currentReceipient.length > 0 ? (
            <Table hover className="table-custom">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                {currentReceipient.map((recipientItem, index) => (
                  <tr key={index}>
                    <td>{recipientItem.customer_name}</td>
                    <td>{recipientItem.email_id}</td>
                    <td>{recipientItem.status}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          ) : (
            <p>No recipients found for this survey.</p>
          )}
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default SurveyResponse;
