import React, { useState, useEffect, useRef } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";
import { jwtDecode } from "jwt-decode";
import axios from "axios";
import Contact from "./components/Contact";
import SurveyResponse from "./components/SurveyResponse";
import Login from "./components/Login";
import UserProfile from "./components/UserProfile";

import Sidebar from "./components/Sidebar";
import ManageTags from "./components/ManageTags";
import AnswerList from "./components/AnswerList";

import SurveyManagement from "./components/SurveyManagement";
import AddSurvey from "./components/AddSurvey";
import EditSurvey from "./components/EditSurvey";
import SendSurvey from "./components/SendSurvey";
import Signup from "./components/Signup";
import Activate from "./components/Activate";

import ResetPassword from "./components/ResetPassword";
import ForgotPassword from "./components/ForgotPassword";
import useScrollTracking from "./hooks/useScrollTracking"; // Import the custom hook
import Cookies from "js-cookie";

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [isLoading, setIsLoading] = useState(true); // Loading state
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(false); // Sidebar collapse state
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const toggleCollapse = () => {
    setIsSidebarCollapsed((prevState) => !prevState);
  };

  const mainContentRef = useRef(null); // Reference for main content area
  const { isScrolledX, isScrolledY } = useScrollTracking(mainContentRef); // Single call with destructured values

  // Resize event listener
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    const checkAuth = async () => {
      console.log("validate token");
      const token = Cookies.get("jwt_token");
      if (token) {
        console.log("Token found:", token);
        // Send JWT token in the request body (using POST request)
        const response = await axios.post(
          "http://35.91.75.215:5000/api/auth/validate-token",
          {
            token: token,
          }
        );
        if (response.status === 201) {
          if (response.data.user_id && response.data.tenant_id) {
            localStorage.setItem("userId", response.data.user_id);
            localStorage.setItem("tenantId", response.data.tenant_id);

            if (response.data.accessToken) {
              localStorage.setItem("accessToken", response.data.accessToken);
              Cookies.set("jwt_token", response.data.accessToken, {
                expires: 7,
                path: "/",
              });

              const decoded = jwtDecode(response.data.accessToken);
              const expirationDate = new Date(decoded.exp * 1000);
              console.log(
                "Access Token expiration time:",
                expirationDate.toString()
              );

              setIsAuthenticated(true);
            }
          }
        } else {
          alert("Session expired. Please log in again.");
          Cookies.remove("jwt_token");
          setIsAuthenticated(false);
          setIsLoading(false);
        }
      } else {
        setIsAuthenticated(false);
      }
      setIsLoading(false);
    };

    checkAuth();
  }, []);

  if (isLoading) {
    return (
      <div className="d-flex justify-content-center align-items-center vh-100">
        <img src="images/spinner.gif" alt="Loading..." className="img-fluid" />
      </div>
    );
  }
  return (
    <Router basename="/csat">
      <div style={{ display: "flex" }}>
        {isAuthenticated && (
          <Sidebar
            setIsAuthenticated={setIsAuthenticated}
            isSidebarCollapsed={isSidebarCollapsed}
            toggleCollapse={toggleCollapse}
            isMobileMenuOpen={isMobileMenuOpen}
            setIsMobileMenuOpen={setIsMobileMenuOpen}
            isScrolledX={isScrolledX}
            isScrolledY={isScrolledY} // Pass the scroll state to Sidebar
          />
        )}
        <div
          ref={mainContentRef} // Ensure this is pointing to the div where content scrolls
          style={{
            flex: 1,
            marginLeft: isAuthenticated
              ? isMobileMenuOpen
                ? "60px"
                : isScrolledX
                ? "0px" // Collapse sidebar when X-axis scroll occurs
                : windowWidth <= 750
                ? "0px"
                : isSidebarCollapsed
                ? "80px"
                : "250px"
              : "0px",
            marginTop: "10px",
            transition: "margin-left 0.3s ease", // Smooth transition for margin-left
            overflowY: "auto", // Enable vertical scrolling
            overflowX: "auto", // Enable horizontal scrolling
            height: "100vh", // Full height
            maxHeight: "100vh", // Ensure content fits within the screen height
            width: "100%", // Ensure the container fills the width of the screen
          }}
        >
          {/* Content goes here */}

          <Routes>
            {/* Routes for authenticated users */}
            <Route
              path="/login"
              element={
                isAuthenticated ? (
                  <Navigate to="/SurveyManagement" />
                ) : (
                  <Login setIsAuthenticated={setIsAuthenticated} />
                )
              }
            />
            <Route
              path="/Signup"
              element={
                isAuthenticated ? (
                  <Navigate to="/SurveyManagement" />
                ) : (
                  <Signup setIsAuthenticated={setIsAuthenticated} />
                )
              }
            />
            <Route
              path="/Activate"
              element={
                isAuthenticated ? (
                  <Navigate to="/SurveyManagement" />
                ) : (
                  <Activate setIsAuthenticated={setIsAuthenticated} />
                )
              }
            />
            <Route
              path="/Reset_password"
              element={
                isAuthenticated ? (
                  <Navigate to="/SurveyManagement" />
                ) : (
                  <ResetPassword setIsAuthenticated={setIsAuthenticated} />
                )
              }
            />
            <Route
              path="/Forgot_password"
              element={
                isAuthenticated ? (
                  <Navigate to="/SurveyManagement" />
                ) : (
                  <ForgotPassword setIsAuthenticated={setIsAuthenticated} />
                )
              }
            />
            <Route
              path="/"
              element={
                isAuthenticated ? (
                  <Navigate to="/SurveyManagement" />
                ) : (
                  <Login setIsAuthenticated={setIsAuthenticated} />
                )
              }
            />
            <Route
              path="/Contact"
              element={isAuthenticated ? <Contact /> : <Navigate to="/" />}
            />
            <Route
              path="/ManageTags"
              element={isAuthenticated ? <ManageTags /> : <Navigate to="/" />}
            />
            <Route
              path="/AnswerList"
              element={isAuthenticated ? <AnswerList /> : <Navigate to="/" />}
            />
            <Route
              path="/SurveyResponse"
              element={
                isAuthenticated ? <SurveyResponse /> : <Navigate to="/" />
              }
            />
            <Route
              path="/SurveyManagement"
              element={
                isAuthenticated ? <SurveyManagement /> : <Navigate to="/" />
              }
            />

            <Route
              path="/AddSurvey"
              element={isAuthenticated ? <AddSurvey /> : <Navigate to="/" />}
            />
            <Route
              path="/EditSurvey"
              element={isAuthenticated ? <EditSurvey /> : <Navigate to="/" />}
            />
            <Route
              path="/SendSurvey"
              element={isAuthenticated ? <SendSurvey /> : <Navigate to="/" />}
            />
            <Route
              path="/UserProfile"
              element={isAuthenticated ? <UserProfile /> : <Navigate to="/" />}
            />
          </Routes>
        </div>
      </div>
    </Router>
  );
};

export default App;
