import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import "../css/Login.css";

const Activate = ({ setIsAuthenticated }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const [error, setError] = useState("");
  const [message, setMessage] = useState("");
  const queryParams = new URLSearchParams(location.search);
  const id = queryParams.get("id");
  const tenant_id = queryParams.get("tenant_id");

  useEffect(() => {
    if (!id || !tenant_id) {
      setError("Invalid or missing id.");
      navigate("/login");
    }
  }, [id, tenant_id, navigate]);

  const handleActivateAccount = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.get(
        `http://35.91.75.215:5000/api/auth/activate`,
        {
          params: {
            id,
            tenant_id,
          },
        }
      );

      if (response.status === 200) {
        setMessage("Account Activation successful!");

        setTimeout(() => {
          navigate("/login");
        }, 2000);
      } else {
        setError("Error during activation.");
      }
    } catch (err) {
      setError("Error during activation.");
      console.error(err);
    }
  };

  return (
    <div className="login_container adaptive-container">
      <div className="row" style={{ width: "100%" }}>
        <div className="col-md-7"></div>
        <form className="col-md-5 sign-in adaptive-form" id="sign_in">
          <div className="login-container adaptive-inner-container">
            <div className="signin-field adaptive-field">
              <div
                className="center-content"
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  textAlign: "center",
                }}
              >
                <div style={{ fontSize: "18px" }}>
                  <p style={{ marginBottom: "10px" }}>Thanks For Signing Up!</p>
                  <p style={{ marginTop: "0", color: "#555" }}>
                    Please click the activate button to complete your sign-up
                    process
                  </p>
                  <button
                    className="btn btn-primary"
                    onClick={handleActivateAccount}
                  >
                    Activate
                  </button>
                  {message && <p style={{ color: "green" }}>{message}</p>}
                  {error && <p style={{ color: "red" }}>{error}</p>}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Activate;
