import axios from "axios";
import { jwtDecode } from "jwt-decode";
import Cookies from "js-cookie";
import { useNavigate } from "react-router-dom";
// Create an Axios instance
const api = axios.create({
  baseURL: "http://35.91.75.215:5000/api/auth", // Your backend API base URL
  headers: {
    "Content-Type": "application/json", // Default content type for JSON
  },
});

// Request interceptor to attach the Authorization header and log token expiration
api.interceptors.request.use(
  (config) => {
    const accessToken = Cookies.get("jwt_token");
    const userId = localStorage.getItem("userId");
    const tenantId = localStorage.getItem("tenantId");
    console.log("Request Config:", config); // Log request to inspect params

    if (
      accessToken &&
      typeof accessToken === "string" &&
      accessToken.trim() !== ""
    ) {
      try {
        const decoded = jwtDecode(accessToken);
        const expirationDate = new Date(decoded.exp * 1000);
        console.log(
          "Current Access Token Expiry Time:",
          expirationDate.toString()
        );

        config.headers["Authorization"] = `Bearer ${accessToken}`;
      } catch (error) {
        console.error("Error decoding the access token:", error);
      }
    }

    if (userId && tenantId) {
      if (
        config.method === "post" ||
        config.method === "put" ||
        config.method === "delete"
      ) {
        if (!config.data || typeof config.data !== "object") {
          config.data = {}; // Ensure config.data exists and is an object
        }
        config.data.user_id = userId;
        config.data.tenant_id = tenantId;
      } else if (config.method === "get") {
        if (!config.params) {
          config.params = {}; // Ensure params exists for GET and DELETE
        }
        config.params.tenant_id = tenantId;
        config.params.user_id = userId;
      }
    }

    return config;
  },
  (error) => Promise.reject(error)
);

// Response interceptor to handle token expiration and retry logic
api.interceptors.response.use(
  (response) => response, // If the response is successful, return it
  async (error) => {
    if (error.response && error.response.status === 403) {
      // Token is invalid or expired, attempt to refresh the token
      try {
        console.log("Access token has expired, attempting to refresh...");

        const newAccessToken = await refreshToken();

        // Log new access token expiry time
        const decoded = jwtDecode(newAccessToken);
        console.log(
          "New Access Token Expiry Time:",
          new Date(decoded.exp * 1000).toString()
        );

        // Update Axios instance with the new token
        api.defaults.headers["Authorization"] = `Bearer ${newAccessToken}`;

        // Retry the original request with the updated instance
        error.config.headers["Authorization"] = `Bearer ${newAccessToken}`;
        return api.request(error.config); // Retry using the Axios instance
      } catch (refreshError) {
        console.error("Error refreshing token:", refreshError);
        logout();
        return Promise.reject(refreshError);
      }
    }
    return Promise.reject(error);
  }
);

// Function to refresh the access token using the refresh token
const refreshToken = async () => {
  const storedRefreshToken = localStorage.getItem("refreshToken");

  if (!storedRefreshToken) {
    console.warn("No refresh token found in storage.");
    return Promise.reject("No refresh token available");
  }

  try {
    console.log("Stored Refresh Token:", storedRefreshToken);

    const response = await axios.post(
      "http://35.91.75.215:5000/api/auth/refresh-token",
      {
        token: storedRefreshToken,
      }
    );

    console.log("Refresh Token Response:", response.data);

    if (response.data && response.data.accessToken) {
      const newAccessToken = response.data.accessToken;
      Cookies.set("jwt_token", newAccessToken);

      return newAccessToken;
    } else {
      throw new Error("No access token returned from refresh endpoint");
    }
  } catch (error) {
    console.error(
      "Error refreshing token:",
      error.response?.data || error.message
    );
    logout(); // Log the user out if refresh fails
    return Promise.reject(error);
  }
};

// Logout function to remove tokens and redirect to login page
const logout = () => {
  console.log("Logging out...");

  // Remove tokens from localStorage
  localStorage.removeItem("accessToken");
  localStorage.removeItem("refreshToken");
  localStorage.removeItem("userId");
  localStorage.removeItem("tenantId");

  Cookies.remove("jwt_token");

  // Update authentication state

  // Redirect to login page
};

export default api;
