import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "../css/Login.css";
const ForgetPassword = ({ setIsAuthenticated }) => {
  const [email, setEmail] = useState("");
  const [error, setError] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const setLoadingState = (state) => {
    setLoading(state);
  };

  const handleChangePassword = async (e) => {
    e.preventDefault();

    if (!email) {
      setError("Email is required.");
      return;
    }

    setLoadingState(true);
    try {
      const response = await axios.post(
        "http://35.91.75.215:5000/api/auth/forgot-password",
        { email }
      );

      if (response.status === 200) {
        setLoadingState(false);
        setMessage("Email sent successfully!");
        setError("");

        setTimeout(() => {
          navigate("/login");
        }, 2000);
      } else {
        setLoadingState(false);
        setError("Error sending email.");
        setMessage("");
      }
    } catch (err) {
      setLoadingState(false);
      if (err.response && err.response.status === 404) {
        setError("Given Email is not available in the database.");
      } else {
        setError("Error sending email.");
      }
      setMessage("");
      console.error(err);
    }
  };

  const handleBackToLogin = () => {
    navigate("/login");
  };

  return (
    <div className="login_container adaptive-container">
      <div className="row" style={{ width: "100%" }}>
        <div className="col-md-7"></div>
        <form
          className="col-md-5 sign-in adaptive-form"
          id="sign_in"
          onSubmit={handleChangePassword}
        >
          <div className="login-container adaptive-inner-container">
            <h1 className="login-title">Forgot Password</h1>

            <div className="signin-field adaptive-field">
              <input
                type="email"
                className="form-control adaptive-input"
                value={email}
                placeholder="Email Address"
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>

            <button
              className="login-button btn-style Centered adaptive-button"
              type="submit"
            >
              Submit
            </button>

            <button
              className="login-button btn-style Centered adaptive-button"
              type="button"
              onClick={handleBackToLogin}
            >
              Back to Login
            </button>

            {loading && (
              <div className="full-screen-overlay">
                <img
                  src="/images/spinner.gif"
                  alt="Loading..."
                  className="spinner"
                />
              </div>
            )}

            {message && (
              <p className="text-success mt-3 text-center adaptive-message">
                {message}
              </p>
            )}

            {error && (
              <p className="text-danger mt-3 text-center adaptive-error">
                {error}
              </p>
            )}
          </div>
        </form>
      </div>
    </div>
  );
};

export default ForgetPassword;
