import { useState, useEffect } from "react";

const useScrollTracking = (ref) => {
  const [isScrolledX, setIsScrolledX] = useState(false); // Horizontal scroll state
  const [isScrolledY, setIsScrolledY] = useState(false); // Vertical scroll state

  useEffect(() => {
    const handleScroll = () => {
      if (ref.current) {
        const { scrollTop, scrollLeft } = ref.current;
        setIsScrolledX(scrollLeft > 0); // Update horizontal scroll state
        setIsScrolledY(scrollTop > 0); // Update vertical scroll state
      }
    };

    // Using setTimeout to ensure DOM has loaded and ref is assigned properly after refresh
    const timer = setTimeout(() => {
      if (ref.current) {
        ref.current.addEventListener("scroll", handleScroll); // Attach the scroll event listener
      }
    }, 100); // Timeout after 100ms to ensure the DOM is ready

    // Cleanup function: remove event listener when component unmounts or ref changes
    return () => {
      clearTimeout(timer); // Cleanup timeout
      if (ref.current) {
        ref.current.removeEventListener("scroll", handleScroll); // Remove scroll listener
      }
    };
  }, [ref]); // Effect runs whenever ref changes

  return { isScrolledX, isScrolledY }; // Return individual scroll states
};

export default useScrollTracking;
