import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Modal, Button } from "react-bootstrap";

import api from "./api";
import "../css/surveyrespose.css";

const SurveyManagement = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [surveyToDelete, setSurveyToDelete] = useState(null);
  const [showDeleteSurveyConfirmation, setShowDeleteSurveyConfirmation] =
    useState(false);
  const [showSurveySuccessModal, setSurveyShowSuccessModal] = useState(false);
  const [surveyactionType, setSurveyActionType] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await api.get("get_all_survey", {
          withCredentials: true, // Configuration object should follow the URL
        });
        setData(response.data.survey_details || []);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
        setError("Error fetching data");
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center vh-100">
        <img src="images/spinner.gif" alt="Loading..." className="img-fluid" />
      </div>
    );
  }

  if (error) {
    return <div>{error}</div>;
  }
  const deleteSurvey = (id) => {
    setSurveyToDelete(id);
    setShowDeleteSurveyConfirmation(true);
  };

  const handledeleteSurvey = async () => {
    try {
      const response = await api.delete("delete_survey", {
        params: {
          survey_id: surveyToDelete,
        },
      });

      if (response.status === 200) {
        setData((prevData) =>
          prevData.filter((item) => item.survey_id !== surveyToDelete)
        );
        setSurveyActionType("delete");
        setSurveyShowSuccessModal(true);
        setShowDeleteSurveyConfirmation(false);
      } else {
        alert("Failed to delete the answer");
      }
    } catch (error) {
      console.error("Error deleting Survey:", error);
      alert("Error deleting Survey. Please try again later.");
    }
  };

  return (
    <div className="container-fluid min-vh-100 d-flex flex-column  py-4">
      <div className="row justify-content-center flex-grow-1">
        <div className="col-md-12">
          <div className="card" style={{ border: "none", boxShadow: "none" }}>
            <div
              className="card-header unique-card-header"
              style={{
                borderBottom: "none",
                marginTop: "48px",
                backgroundColor: "transparent",
                display: "flex", // Flexbox to control layout
                justifyContent: "space-between", // Header left and button right by default
                flexWrap: "wrap", // Allow items to wrap on smaller screens
              }}
            >
              <h4 className="surveyHeader  unique-survey-header">
                Survey Management
              </h4>
              <Link
                className="btn unique-create-survey-btn"
                style={{
                  backgroundColor: "#0a2472",
                  color: "white",
                  marginTop: "8px", // Space when wrapped
                }}
                to="/AddSurvey"
              >
                Create Survey
              </Link>
            </div>

            <div className="card-body">
              {data.length === 0 ? (
                <div className="text-center py-4">
                  <p className="fs-3 noData">
                    <span className="text-danger">No surveys found!</span> Add a
                    new survey to get started.
                  </p>
                </div>
              ) : (
                <table className="surveyTable" style={{ textAlign: "center" }}>
                  <thead>
                    <tr style={{ textAlign: "center" }}>
                      <th style={{ textAlign: "center" }}>Created Date</th>
                      <th style={{ textAlign: "center" }}>Survey Name</th>
                      <th style={{ textAlign: "center" }}>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {data.map((item, index) => (
                      <tr
                        key={item.survey_id || index}
                        style={{ height: "80px" }}
                      >
                        <td style={{ textAlign: "center" }}>
                          {new Date(item.created_at)
                            .toISOString()
                            .replace("T", " ")
                            .substring(0, 19)}
                        </td>

                        <td style={{ textAlign: "center" }}>
                          {item.survey_name}
                          <br />
                          {(item.sent_status === "1" ||
                            item.sent_status === "0") && (
                            <span className="survey-warning">
                              * This survey is already in progress you cannot
                              edit.
                            </span>
                          )}
                        </td>

                        <td style={{ textAlign: "center" }}>
                          <Link
                            type="button"
                            to="/EditSurvey"
                            state={{ surveyId: item.survey_id }}
                            className="btn btn-default btn-edit"
                            style={{
                              pointerEvents:
                                item.sent_status === 0 ||
                                item.sent_status === "0"
                                  ? "none"
                                  : "auto",
                            }}
                          >
                            <img
                              src="images/icon/edit-1.png"
                              alt="Edit"
                              className="img-centered img-fluid"
                            />
                          </Link>

                          <Link
                            className="btn btn-default btn-delete"
                            onClick={() => deleteSurvey(item.survey_id)}
                          >
                            <img
                              src="images/icon/Removenew.svg"
                              alt="Delete"
                              className="img-centered img-fluid"
                            />
                          </Link>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </div>
          </div>
          <Modal
            show={showDeleteSurveyConfirmation}
            onHide={() => setShowDeleteSurveyConfirmation(false)}
            style={{ marginTop: "50px" }}
          >
            <Modal.Header
              closeButton
              style={{ justifyContent: "center", width: "100%" }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Delete Survey
              </Modal.Title>
            </Modal.Header>
            <Modal.Body
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center",
              }}
            >
              Are you sure you want to delete the Survey?
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "100%",
                  maxWidth: "300px",
                  margin: "0 auto",
                }}
              >
                <Button
                  variant="danger"
                  onClick={handledeleteSurvey}
                  style={{ marginBottom: "10px", width: "100%" }}
                >
                  Confirm
                </Button>
                <Button
                  variant="secondary"
                  onClick={() => setShowDeleteSurveyConfirmation(false)}
                  style={{ width: "100%" }}
                >
                  Cancel
                </Button>
              </div>
            </Modal.Footer>
          </Modal>

          <Modal
            show={showSurveySuccessModal}
            onHide={() => setSurveyShowSuccessModal(false)}
            style={{ marginTop: "50px" }}
          >
            <Modal.Header
              closeButton
              style={{ justifyContent: "center", width: "100%" }}
            >
              <Modal.Title style={{ textAlign: "center", width: "100%" }}>
                Success
              </Modal.Title>
            </Modal.Header>
            <Modal.Body style={{ textAlign: "center" }}>
              {surveyactionType === "delete"
                ? "Survey Deleted Successfully!"
                : "Action Successful!"}
            </Modal.Body>
            <Modal.Footer
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
              }}
            >
              <Button
                variant="success"
                onClick={() => {
                  setSurveyShowSuccessModal(false);
                  // Navigate to Survey Management
                }}
              >
                close
              </Button>
            </Modal.Footer>
          </Modal>
        </div>
      </div>
    </div>
  );
};

export default SurveyManagement;
