import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "../css/Login.css";
import { jwtDecode } from "jwt-decode";
import PropTypes from "prop-types";
import Cookies from "js-cookie";
const Login = ({ setIsAuthenticated }) => {
  const [tenant_name, setTenantName] = useState(""); // Updated to match backend
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();
  const [message, setMessage] = useState("");
  const [formErrors, setFormErrors] = useState({});
  const [jwt_token, setJwt_token] = useState("");

  const handleLogin = async (e) => {
    e.preventDefault();
    setMessage("");
    setError("");
    setFormErrors({});

    if (!tenant_name && !email && !password) {
      setFormErrors({
        tenant_name: "The tenant name is required",
        email: "The email is required",
        password: "The password is required",
      });
      return;
    }

    let errors = {};
    setFormErrors({});

    if (!tenant_name) errors.tenant_name = "Tenant Name is required";
    if (!email) errors.email = "Email is required";
    if (!password) errors.password = "Password is required";

    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }

    try {
      const response = await axios.post(
        "http://35.91.75.215:5000/api/auth/login",
        { email, password, tenant_name },
        {
          headers: { "Content-Type": "application/json" },
        }
      );

      // Log cookies after login
      console.log("Cookies after login:", document.cookie);
      console.log("Response Headers:", response.headers);
      if (response.data.user_id && response.data.tenant_id) {
        localStorage.setItem("userId", response.data.user_id);
        localStorage.setItem("tenantId", response.data.tenant_id);
      }

      if (response.data.accessToken && response.data.refreshToken) {
        localStorage.setItem("accessToken", response.data.accessToken);
        localStorage.setItem("refreshToken", response.data.refreshToken);
        setIsAuthenticated(true);

        // Check expiration of access token
        const decoded = jwtDecode(response.data.accessToken);
        const expirationDate = new Date(decoded.exp * 1000);
        console.log("Access Token expiration time:", expirationDate.toString());

        Cookies.set("jwt_token", response.data.accessToken, {
          expires: 7, // Cookie expiration in days
          path: "/",
        });

        // Read the cookie
        const storedCookie = Cookies.get("jwt_token");
        setJwt_token(storedCookie || "No Cookie Found");

        console.log("Stored Cookie:", storedCookie);

        navigate("/"); // Navigate to desired page after login
      }
    } catch (error) {
      console.error("Error during login:", error);
      setMessage(error.response ? error.response.data.message : "Login failed");
    }
  };

  return (
    <div className="login_container">
      <div className="row" style={{ width: "100%" }}>
        <div className="col-md-7"></div>
        <form className="col-md-5 sign-in" id="sign_in" onSubmit={handleLogin}>
          <div
            className="login-container"
            style={{ width: "500 px", marginRight: "0px" }}
          >
            <h1 className="login-title">Login</h1>
            <div className="signin-field">
              <input
                type="text"
                className="form-control"
                value={tenant_name}
                placeholder="Tenant Name"
                onChange={(e) => setTenantName(e.target.value)}
              />
              {formErrors.tenant_name && (
                <div className="error-messages">{formErrors.tenant_name}</div>
              )}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="22"
                height="25"
                viewBox="0 0 22 25"
                fill="none"
              >
                <path
                  d="M8.34687 14.4825V15.3282C8.34687 17.0233 9.65365 18.4206 11.2838 18.4206C12.9139 18.4206 14.2207 17.0233 14.2207 15.3282V14.4825C14.2207 12.7874 12.9139 11.3901 11.2838 11.3901C9.65365 11.3901 8.34687 12.7874 8.34687 14.4825ZM13.3246 14.4825V15.3282C13.3246 16.5338 12.3983 17.4964 11.2837 17.4964C10.1692 17.4964 9.24281 16.5338 9.24281 15.3282V14.4825C9.24281 13.2769 10.1692 12.3143 11.2837 12.3143C12.3983 12.3143 13.3246 13.2769 13.3246 14.4825Z"
                  fill="#424242"
                  stroke="#424242"
                  strokeWidth="0.4"
                ></path>
                <path
                  d="M1.60739 23.4096H5.93772V24.344C5.93772 24.5899 6.1289 24.8062 6.38574 24.8062H16.1805C16.4388 24.8062 16.6285 24.5893 16.6285 24.344V23.4096H20.9589C21.2172 23.4096 21.4069 23.1928 21.4069 22.9475L21.4059 9.4005C21.4059 9.26362 21.3471 9.12815 21.236 9.03843C21.2358 9.03827 21.2356 9.03811 21.2354 9.03794L11.5617 1.14371L11.5616 1.14367C11.3989 1.01096 11.1683 1.01097 11.0056 1.14366L11.0055 1.1437L1.33082 9.03794C1.33063 9.0381 1.33044 9.03825 1.33025 9.03841C1.22012 9.12738 1.15937 9.26239 1.15937 9.40157V22.9475C1.15937 23.1933 1.35056 23.4096 1.60739 23.4096ZM6.83384 23.8809V22.3464C6.83384 20.8562 7.97819 19.6632 9.36205 19.6632H13.2044C14.5882 19.6632 15.7326 20.8562 15.7326 22.3464V23.8809H6.83384ZM20.5099 22.4853H16.6286V22.3474C16.6286 20.3687 15.1029 18.74 13.2044 18.74H9.36205C7.46352 18.74 5.9378 20.3687 5.9378 22.3474V22.4853H2.05649V9.62463L11.2832 2.09539L20.5099 9.62463V22.4853Z"
                  fill="#424242"
                  stroke="#424242"
                  strokeWidth="0.4"
                ></path>
              </svg>
            </div>

            <div className="signin-field">
              <input
                type="text"
                className="form-control"
                value={email}
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
              />
              {formErrors.email && (
                <div className="error-messages">{formErrors.email}</div>
              )}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="23"
                height="18"
                viewBox="0 0 23 18"
                fill="none"
              >
                <path
                  d="M21.2425 0.55625H1.58226C0.845712 0.55625 0.259375 1.19468 0.259375 1.96641V15.786C0.259375 16.5578 0.845712 17.1962 1.58226 17.1962H21.2425C21.979 17.1962 22.5654 16.5578 22.5654 15.786V1.96641C22.5654 1.19733 21.9792 0.55625 21.2425 0.55625ZM21.7375 15.7177L15.275 8.9623L21.7375 2.03869V15.7177ZM21.1464 1.42896L13.3688 9.76414C12.2889 10.9211 10.5358 10.9211 9.45596 9.76414L1.6783 1.42896H21.1464ZM1.08493 15.7179V2.03617L7.55201 8.96478L1.08493 15.7179ZM1.67743 16.326L8.13315 9.58739L8.86763 10.3743C9.56856 11.1252 10.4887 11.502 11.4124 11.502C12.336 11.502 13.2562 11.1252 13.9571 10.3743L14.6962 9.58241L21.1475 16.326H1.67743Z"
                  fill="#424242"
                  stroke="#424242"
                  strokeWidth="0.2"
                ></path>
              </svg>
            </div>

            <div className="signin-field">
              <input
                type="password"
                className="form-control"
                value={password}
                placeholder="Password"
                onChange={(e) => setPassword(e.target.value)}
              />
              {formErrors.password && (
                <div className="error-messages">{formErrors.password}</div>
              )}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="27"
                height="28"
                viewBox="0 0 27 28"
                fill="none"
              >
                <path
                  d="M5.54031 8.31616C4.90618 7.64717 4.90452 6.55923 5.5383 5.88827C6.17132 5.21811 7.20077 5.21636 7.83566 5.88615C8.46979 6.55514 8.47145 7.64308 7.83766 8.31404C7.20464 8.9842 6.17519 8.98595 5.54031 8.31616ZM7.30572 6.44718C6.96532 6.08808 6.40893 6.08859 6.06913 6.44832C5.72933 6.80806 5.72982 7.39606 6.07021 7.75517C6.41061 8.11428 6.96699 8.11376 7.30679 7.75403C7.6466 7.39429 7.64611 6.80629 7.30572 6.44718Z"
                  fill="#424242"
                ></path>
                <path
                  d="M21.7937 22.5561L21.7687 22.5274L21.8218 22.8666C21.8226 22.8717 21.8223 22.8748 21.8221 22.8765C21.8218 22.8782 21.8213 22.8797 21.8206 22.8812L21.7937 22.5561ZM21.7937 22.5561L21.5472 20.6849M21.7937 22.5561L21.5472 20.6849M21.5472 20.6849L19.7754 18.8157L19.2893 19.0505C19.0136 19.1853 18.6941 19.1243 18.4774 18.8957C18.2601 18.6665 18.2054 18.3317 18.3291 18.039L18.3293 18.0383L18.5508 17.5238L18.1705 17.1226L17.6845 17.3574C17.4088 17.4922 17.0892 17.4313 16.8726 17.2027C16.6553 16.9735 16.6006 16.6386 16.7242 16.3459L16.7245 16.3453L16.946 15.8308L16.6835 15.5539L16.1975 15.7887C15.9218 15.9235 15.6022 15.8625 15.3856 15.6339C15.1683 15.4047 15.1136 15.0699 15.2372 14.7772L15.2375 14.7765L15.459 14.262L12.0349 10.6496L11.9883 10.6005L12.0142 10.5365L12.1139 10.2906C12.1139 10.2905 12.114 10.2903 12.114 10.2902C12.8345 8.48574 12.4556 6.42772 11.1445 5.04453C10.2569 4.10815 9.07629 3.61133 7.82375 3.64886C6.57113 3.68639 5.41823 4.25016 4.57474 5.24128C2.99309 7.1024 3.07616 10.0464 4.75096 11.8133L4.77065 11.834C6.07799 13.1973 8.02004 13.5877 9.71488 12.8272C9.71497 12.8272 9.71505 12.8271 9.71513 12.8271L9.94769 12.7213L10.0082 12.6938L10.0548 12.7429L18.3778 21.5235L21.7855 22.896C21.7855 22.896 21.7855 22.8961 21.7856 22.8961C21.7896 22.8976 21.7924 22.898 21.7943 22.8981C21.7963 22.8982 21.7984 22.8979 21.8006 22.8973C21.8053 22.8959 21.8098 22.8932 21.8128 22.8909L21.5472 20.6849ZM17.7542 22.2822L9.79467 13.885C7.82086 14.6315 5.62365 14.1278 4.11772 12.5603L4.11772 12.5603L4.11725 12.5598L4.09354 12.5348C2.06758 10.3975 1.96866 6.84052 3.88015 4.58969C4.89501 3.39355 6.28921 2.7094 7.80242 2.66617C9.31792 2.62041 10.745 3.22208 11.8172 4.35315C13.3232 5.94198 13.8128 8.27313 13.1047 10.3758L16.5483 14.0087L16.5962 14.0593L16.568 14.1243L16.3488 14.6289L16.826 14.3965L16.8875 14.3665L16.9354 14.417L18.03 15.5718L18.0779 15.6224L18.0496 15.6874L17.8305 16.1921L18.3076 15.9596L18.3691 15.9296L18.417 15.9801L19.6294 17.2592L19.6773 17.3097L19.6491 17.3748L19.4299 17.8794L19.9071 17.6469L19.9686 17.617L20.0165 17.6675L22.2568 20.0311C22.3673 20.1476 22.44 20.3006 22.4606 20.4663C22.4606 20.4664 22.4606 20.4665 22.4607 20.4667L22.7582 22.7424C22.7962 23.0303 22.7188 23.3137 22.5497 23.5319L22.5923 23.5378L22.4127 23.6886C22.1411 23.9168 21.7866 23.9733 21.4611 23.8459L21.4602 23.8455L18.0078 22.4547L18.0077 22.4547C17.9138 22.4168 17.8271 22.3591 17.7542 22.2822Z"
                  fill="#424242"
                  stroke="#424242"
                  strokeWidth="0.2"
                ></path>
              </svg>
            </div>
            <div className="frg-pass">
              <a className="d-block small mt-2" href="/Forgot_password">
                Forgot Password
              </a>
            </div>

            <button className="login-button btn-style Centered" type="submit">
              Sign In
            </button>
            <div className="signup-page">
              <a href="/signup">
                New User? <span>Create An Account</span>
              </a>
            </div>
            {message && (
              <p className="text-danger mt-3 text-center">{message}</p>
            )}

            {error && <p className="error-message">{error}</p>}
          </div>
        </form>
      </div>
    </div>
  );
};
Login.propTypes = {
  setIsAuthenticated: PropTypes.func.isRequired, // Ensure that the prop is a function
};
export default Login;
